<template><section><h1>LabelGraphics</h1>
<p>The <code v-pre>vc-graphics-label</code> component is used to load a two dimensional label. Need to be used as a subcomponent of <code v-pre>vc-entity</code>.</p>
<h2>Example</h2>
<h3>Load LabelGraphics with <code v-pre>vc-graphics-label</code></h3>
<h4>Preview</h4>
<doc-preview>
 <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :position="position1" :description="description" :label.sync="label1">
          <vc-graphics-label :text="description" font="20px sans-serif" :pixelOffset="pixelOffset1"></vc-graphics-label>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>Code</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position1"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:label.sync</span>=<span class="hljs-string">"label1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-label</span> <span class="hljs-attr">:text</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">font</span>=<span class="hljs-string">"20px sans-serif"</span> <span class="hljs-attr">:pixelOffset</span>=<span class="hljs-string">"pixelOffset1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-label</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">label1</span>: {},
        <span class="hljs-attr">pixelOffset1</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">0.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">20</span> },
        <span class="hljs-attr">position1</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">114.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300000.0</span> }
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> A boolean Property specifying the visibility of the label.</td>
</tr>
<tr>
<td>text</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the text. Explicit newlines '\n' are supported.</td>
</tr>
<tr>
<td>font</td>
<td>String</td>
<td><code v-pre>'30px sans-serif'</code></td>
<td><code v-pre>optional</code> A Property specifying the CSS font.</td>
</tr>
<tr>
<td>labelStyle</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> A Property specifying the LabelStyle. <strong>FILL: 0, OUTLINE: 1, FILL_AND_OUTLINE: 2</strong></td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> A numeric Property specifying the scale to apply to the text.</td>
</tr>
<tr>
<td>showBackground</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> A boolean Property specifying the visibility of the background behind the label.</td>
</tr>
<tr>
<td>backgroundColor</td>
<td>Object|String|Array</td>
<td><code v-pre>[0.165, 0.165, 0.165, 0.8]</code></td>
<td><code v-pre>optional</code> A Property specifying the background Color.</td>
</tr>
<tr>
<td>backgroundPadding</td>
<td>Object</td>
<td><code v-pre>{x: 7, y: 5}</code></td>
<td><code v-pre>optional</code> A Cartesian2 Property specifying the horizontal and vertical background padding in pixels. <strong>结构：{ x: number, y: number }</strong></td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Object</td>
<td><code v-pre>{x: 0, y: 0 }</code></td>
<td><code v-pre>optional</code> A Cartesian2 Property specifying the pixel offset. <strong>structure: { x: number, y: number }</strong></td>
</tr>
<tr>
<td>eyeOffset</td>
<td>Object</td>
<td><code v-pre>{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre>optional</code> A Cartesian3 Property specifying the eye offset. <strong>structure: { x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>horizontalOrigin</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> A Property specifying the HorizontalOrigin.</td>
</tr>
<tr>
<td>verticalOrigin</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> A Property specifying the VerticalOrigin.</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> A Property specifying what the height is relative to.</td>
</tr>
<tr>
<td>fillColor</td>
<td>Object|String|Array</td>
<td><code v-pre>white</code></td>
<td><code v-pre>optional</code> A Property specifying the fill Color.</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>black</code></td>
<td><code v-pre>optional</code> A Property specifying the outline Color.</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> A numeric Property specifying the outline width.</td>
</tr>
<tr>
<td>translucencyByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> A NearFarScalar Property used to set translucency based on distance from the camera. <strong>structure: { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>pixelOffsetScaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> A NearFarScalar Property used to set pixelOffset based on distance from the camera. <strong>structure: { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>scaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> A NearFarScalar Property used to set scale based on distance from the camera. <strong>structure: { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying at what distance from the camera that this label will be displayed. <strong>structure: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>disableDepthTestDistance</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the distance from the camera at which to disable the depth test to.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/LabelGraphics.html">LabelGraphics</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>Triggers whenever a property or sub-property is changed or modified.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data () {
        return {
          description: 'Hello Vue Cesium',
          label1: {},
          pixelOffset1: { x: 0.0, y: 20},
          position1: { lng: 114.0, lat: 40.0, height: 300000.0 }
        }
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
        }
      }
    }
  </script>